}
table, th, td {
padding: 15px;

    text-align: left;
}




th, td{
   
        font-size: 16px;
        line-height: 24px;
        font-style: normal;
        font-variant: normal;
        font-weight: 700;
        letter-spacing: normal;
        text-decoration: none solid rgb(194, 194, 194);
        text-align: left;
        text-indent: 0px;
        text-transform: none;
        vertical-align: baseline;
        white-space: normal;
        word-spacing: 0px;
    
        color: rgb(194, 194, 194);
    
    } 
   


    .grid-table {
        display: grid;
        grid-template-columns: auto auto auto; 
